<template lang="html">
  <div class="row">
    <div class="col-lg-8 col-md-8 col-sm-12">
      <form-card
        v-for="group of centerGroups"
        :group="group"
        v-model="gallery"
        @updateHandle="updateHandle"
      ></form-card>
      <custom-field
        v-if="customField.length > 0"
        :group="customField"
        :value="custom_field"
        v-model="custom_field"
      ></custom-field>
      <div class="card">
        <div class="card-header">
          <h6 class="title pull-left">Danh sách photo</h6>
          <el-button type="primary" class="pull-right" @click="createPhoto">Thêm Photo</el-button>
        </div>
        <div class="card-body form-card">
          <my-table :columnDefs="columnDefs" v-bind:data-rows="photos" :actions="actions" :actionsTable="actionsTable"/>
        </div>
      </div>
    </div>
    <div class="col-lg-4 col-md-4 col-sm-12">
      <form-card
        v-for="group of sideGroups"
        hasTop="true"
        :group="group"
        v-model="gallery"
      >
      </form-card>
      <div class="card" v-if="listView">
        <div class="card-header">
          <h6 class="title">Giao diện</h6>
        </div>
        <div class="card-body form-card">
          <div class="row">
            <div class="col-sm-12">
              <my-select
                class="full-width"
                :attribute="listView"
                placeholder="Giao diện"
                v-model="form.template"
              >
              </my-select>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid">
      <div class="page-action">
        <el-button @click="cancel">Hủy</el-button>
        <el-button type="primary" @click="save">Cập nhật</el-button>
      </div>
    </div>
    <el-dialog
      title="Tạo Photo"
      :visible.sync="dialogFormCreateVisible"
      width="40%" @keyup.enter.native="storePhoto"
    >
      <el-form :model="formCreatePhoto" label-width="120px">
        <el-form-item label="Tiêu đề:">
          <el-input
            autocomplete="off"
            v-model="formCreatePhoto.title"
            placeholder="Tiêu đề"
            :class="errors.has('name')?'border-danger':''"
            v-validate="'required'"
            data-vv-name="name"
            data-vv-as="Tiêu đề"
          ></el-input>
          <span class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</span>
        </el-form-item>
        <el-form-item label="Mô tả">
          <editor
            v-model="formCreatePhoto.description"
            apiKey="pgm9gcnllfimfcgo7joyxpm22j4ov87erg4u4fyy7gxo1g23"
          ></editor>
        </el-form-item>
        <el-form-item label="Hình ảnh">
          <my-image
            v-model="formCreatePhoto.image"
          ></my-image>

        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormCreateVisible = false">Đóng</el-button>
        <el-button type="primary" @click="storePhoto">Tạo</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="Chỉnh sửa Photo"
      :visible.sync="dialogFormEditVisible"
      width="40%" @keyup.enter.native="updatePhoto"
    >
      <el-form :model="formEditPhoto" label-width="120px">
        <el-form-item label="Gallery:">
          <my-select
            class="full-width"
            placeholder="Gallery"
            :attribute="galleries"
            v-model="formEditPhoto.gallery_id"
          >
          </my-select>
        </el-form-item>
        <el-form-item label="Tiêu đề:">
          <el-input
            autocomplete="off"
            v-model="formEditPhoto.title"
            placeholder="Tiêu đề"
            :class="errors.has('name')?'border-danger':''"
            v-validate="'required'"
            data-vv-name="name"
            data-vv-as="Tiêu đề"
          ></el-input>
          <span class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</span>
        </el-form-item>
        <el-form-item label="Mô tả">
          <editor
            v-model="formEditPhoto.description"
            apiKey="pgm9gcnllfimfcgo7joyxpm22j4ov87erg4u4fyy7gxo1g23"
          ></editor>
        </el-form-item>
        <el-form-item label="Hình ảnh">
          <my-image
            v-model="formEditPhoto.image"
            :value="formEditPhoto.image"
          ></my-image>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormEditVisible = false">Đóng</el-button>
        <el-button type="primary" @click="updatePhoto">Cập nhật</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import FormCard from 'src/components/UIComponents/FormCard.vue'
import MyTable from 'src/components/UIComponents/Table.vue'
import CustomField from 'src/components/UIComponents/CustomField.vue'
import dtHelper from 'src/helpers/datatable'
import photoSchemas from './photo-schemas'
import { MessageBox, Message, Input, Button, Dialog, Form, FormItem, } from 'element-ui';
import MyImage from 'src/components/UIComponents/Image.vue'
import MySelect from 'src/components/UIComponents/Select'
import Editor from '@tinymce/tinymce-vue'
import dataFrom from './gallery-form'
import { mapState } from 'vuex'

let _form = {}, _custom_field = {}, firstGroups, secondGroups;

export default {
  components: {
    FormCard,
    ElInput: Input,
    ElButton: Button,
    ElForm: Form,
    ElFormItem: FormItem,
    ElDialog: Dialog,
    MyTable,
    MyImage,
    Editor,
    MySelect,
    CustomField
  },
  beforeCreate() {
    firstGroups = dataFrom[0].groups;
    secondGroups = dataFrom[1].groups;

    if (this.$store.state.customFieldDefine.gallery){
      _custom_field = this.$store.state.customFieldDefine.gallery.reduce(function (result, item) {
        result[item.handle] = '';
        return result;
      }, {});
    }

    firstGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })
    secondGroups.forEach(group => {
      group.attributes.forEach(attr => {
        if (attr.multiple && _form[attr.prop]) {
          _form[attr.prop] = [];
        } else {
          _form[attr.prop] = '';
        }
        attr.value = _form[attr.prop];
      })
    })
    let id = this.$route.params.id;
    this.$store.dispatch('getCustomField', {
      type: 'gallery',
      id: id
    });
    this.$store.dispatch('getListThemeView', 'gallery');
  },
  data () {
    return {
      centerGroups: firstGroups,
      sideGroups: secondGroups,
      columnDefs: dtHelper.buildColumDefs(photoSchemas),
      actions: [
        {
          type: 'primary',
          icon: 'nc-icon nc-ruler-pencil',
          callback: this.editPhoto
        },
        {
          type: 'danger',
          icon: 'nc-icon nc-simple-remove',
          callback: this.removePhoto
        },
      ],
      actionsTable: [
        {
          title: 'Ẩn',
          callback: this.inactiveAll
        },
        {
          title: 'Hiện',
          callback: this.activeAll
        },
        {
          title: 'Xóa',
          color: 'text-danger',
          callback: this.removeAll
        }
      ],
      form: _form,
      gallery: [],
      dialogFormCreateVisible: false,
      dialogFormEditVisible: false,
      formCreatePhoto: {},
      formEditPhoto: {
        title: '',
        description: '',
        image: '',
        gallery_id: ''
      },
      custom_field: _custom_field,
      show_fields: []
    }
  },
  computed: {
    ...mapState({
      originalForm: state => state.galleryDetail,
      photos: state => state.photos,
    }),
    customField: {
      get() {
        let data = this.$util.filterByHandle(this.$store.state.customField, this.show_fields);
        for (let item of data) {
          if (item.value) {
            _custom_field[item.handle] = item.value;
          } else {
            _custom_field[item.handle] = '';
          }
        }
        return data||'';
      },
      set(value) {}
    },
    listView() {
      let self = this;
      let listTheme = self.$store.state.listThemeView;
      if (listTheme.length) {
        listTheme.unshift({
          title: 'Mặc định',
          value: ''
        })
        return {
          options: listTheme
        };
      }
      return '';
    },
    galleries() {
      let data = this.$store.state.galleries;
      let arr = {};
      arr.options = [];
      for (let item of data) {
        arr.options.push({
          value: item.id,
          title: item.title
        });
      }
      return arr;
    },
  },

  mounted() {
    this.getContext();
    let id = this.$route.params.id;
    this.$store.dispatch('fetchGalleryDetail', id);
    this.$store.dispatch('fetchGalleries');

    this.$store.dispatch('setPageTitle', 'Chỉnh sửa gallery');
    this.$store.dispatch('setCurrentActions', [{
      label: 'Xóa',
      type: 'warning',
      icon: '',
      callback: this.remove
    }, {
      label: 'Cập nhật',
      type: 'primary',
      icon: '',
      callback: this.save
    }])
  },
  methods: {
    createPhoto() {
      this.$router.push('/photo/' + this.gallery.id + '/create');
    },
    cancel() {
      this.$router.push({name: 'Allgalleries'});
    },
    remove(){
      let self = this;
      MessageBox.confirm('Bạn có chắc chắn gallery không?', 'Warning', {
        confirmButtonText: 'Đồng ý',
        cancelButtonText: 'Hủy bỏ',
        type: 'warning',
        center: true
      }).then(() => {
        self.$store.dispatch('removeGallery', self.gallery.id).then(res => {
          Message({
            type: 'success',
            message: 'Đã xóa gallery'
          });
          self.$router.push({name: 'Allgalleries'});
        });
      }).catch(() => {
        Message({
          type: 'info',
          message: 'Hủy bỏ'
        });
      })
    },
    view(){
      let self = this;
      var win = window.open(self.gallery.url, '_blank');
      if (win) {
        win.focus();
      } else {
        Message({
          message: 'Please allow popups for this website',
          type: 'danger'
        });
      }
    },
    editPhoto(index, row){
      this.$router.push('/photo/' + this.gallery.id + '/' + row.id);
    },
    removePhoto(index, row){
      let self = this;
      MessageBox.confirm('Bạn có chắc chắn xóa không?', 'Warning', {
        confirmButtonText: 'Đồng ý',
        cancelButtonText: 'Hủy bỏ',
        type: 'warning',
        center: true
      }).then(() => {
        this.$store.dispatch('removePhoto', row.id).then(res => {
          Message({
            type: 'success',
            message: 'Đã xóa hình ảnh'
          });
          let id = self.$route.params.id;
          self.$store.dispatch('fetchGalleryDetail', id);
        });
      }).catch(() => {
        Message({
          type: 'info',
          message: 'Hủy bỏ'
        });
      })
    },
    inactiveAll(rows){
      this.updateStatus(rows, 'inactive');
    },
    activeAll(rows){
      this.updateStatus(rows, 'active');
    },
    removeAll(rows){
      let self = this;
      rows.forEach(function (row, index) {
        self.$store.dispatch('removePhoto', row.id).then(res => {
          Message({
            type: 'success',
            message: 'Đã xóa hình ảnh'
          });
        });
      })
      let id = self.$route.params.id;
      self.$store.dispatch('fetchGalleryDetail', id);
    },
    updateStatus(rows, status){
      let self = this;
      this.$util.updateStatusAll('photo', rows, status).then(function (result) {
        let id = self.$route.params.id;
        self.$store.dispatch('fetchGalleryDetail', id);
        Message({
          type: 'success',
          message: 'Cập nhật thành công'
        });
      }).catch(function (error) {
        Message({
          type: 'error',
          message: error.message
        });
      });
    },
    storePhoto() {
      let self = this;
      let data = self.formCreatePhoto;
      data.gallery_id = self.gallery.id;
      self.$store.dispatch('storePhoto', data).then(result => {
        Message({
          type: 'success',
          message: 'Tạo mới thành công'
        });
        let id = self.$route.params.id;
        self.$store.dispatch('fetchGalleryDetail', id);
        self.dialogFormCreateVisible = false;
        self.formCreatePhoto = {};
      }).catch(error => {
        Message({
          type: 'error',
          message: error.message
        });
      });
    },
    updatePhoto() {
      let self = this;
      let data = self.formEditPhoto;
      self.$store.dispatch('updatePhoto', data).then(result => {
        Message({
          type: 'success',
          message: 'Chỉnh sửa thành công'
        });
        let id = self.$route.params.id;
        self.$store.dispatch('fetchGalleryDetail', id);
        self.dialogFormEditVisible = false;
        self.formEditPhoto = {};
      }).catch(error => {
        Message({
          type: 'error',
          message: error.message
        });
      });
    },
    updateHandle(){
      let self = this;
      let data = {
        handle: self.$util.createHandle(self.form.title),
        lang: 'vi'
      }
      self.$store.dispatch('checkHandle', data).then(result => {
        self.$store.state.galleryDetail = self.form;
        self.$store.state.galleryDetail.handle = result;
      });
    },
    save() {
      let self = this;
      this.$validator.validateAll().then(function (result) {
        if (result) {
          if (self.gallery.parent) {
            self.gallery.parent_id = self.gallery.parent.id;
          }
          self.$store.dispatch('updateGallery', self.gallery).then(function (result) {
            self.$store.dispatch('storeCustomField', self.$util.parseDataCustomfield(self.customField, self.custom_field, 'gallery', self.gallery.id));

            Message({
              type: 'success',
              message: 'Cập nhật thành công'
            });
          }).catch(function (error) {
            Message({
              type: 'error',
              message: error.message
            });
          });
        }
      })
    },
    getContext() {
      let ctx = this.$util.getContext(this.form, {module: 'galleries'});
      this.$store.state.ctx = ctx;
      return ctx;
    },
  },
  watch: {
    originalForm(newVal, oldVal){
      this.gallery = newVal;
    },
    'gallery': {
      handler(newVal, oldVal) {
        let ctx = this.getContext();
        let output = window.__FUNC.CustomField(ctx);
        this.show_fields = output;
      },
      deep: true
    }
  },
  destroyed(){
    this.$store.dispatch('setCurrentActions', []);
    this.$store.state.galleryDetail = {};
    this.$store.state.listThemeView = [];
    this.$store.state.customField = [];
  }
}
</script>
